<template>
  <div>
    <transition enter-active-class="animated  fadeInLeft" leave-active-class="animated fadeOutRight">
      <div class="detail" v-show="showdetailflag">
        <!-- 背景模糊设置 -->
        <div class="background"> </div>
        <div class="sellerinfo"> {{seller.name}}</div>
          <!-- 星星组件 -->
        <star :score="scorestar"> </star>
        <p>{{seller.score}}</p>
          <!-- 线条设置 -->
        <div class="lineshow">
          <div class="lineone"></div>
          <p>商家信息</p>
          <div class="linetwo"></div>
            <!-- 商户优惠 -->
          <div class="sellerinfoshow">
            <div class="infoshowimg" v-for="(v,k) in (seller.supports)" :key="k">
              <img v-show="v.type==0" src="../../../static/images/decrease.png">
              <img v-show="v.type==1" src="../../../static/images/discount.png">
              <img v-show="v.type==2" src="../../../static/images/guarantee.png">
              <img v-show="v.type==3" src="../../../static/images/invoice.png">
              <img v-show="v.type==4" src="../../../static/images/special.png">
              <p>{{v.description}}</p>
            </div>
             <!-- 商户公告 -->
            <div class="announcement">
              <div class="linethree"></div>
              <p>商家公告</p>
              <div class="linefour"></div>
              <div class="sellerinfoshow">
                <div class="infoshowimg">
                  {{seller.bulletin}}
                </div>
              </div>
            </div>
          </div>
        </div>
         <!-- 关闭按钮 -->
        <div @click="closeshow" class="closebutton"> X</div>
      </div>
    </transition>
  </div>
</template>
</div>
<script>
import animate from 'animate.css'
import Star from '../star/star'
import { mapGetters } from 'vuex'
export default {
  computed: {
    ...mapGetters([
      'showdetailflag',
      'seller'
    ]),
    //当前用户的评分
    scorestar() {
      return this.seller.score
    },
  },
  components: {
    Star
  },
  //关闭按钮
  methods: {
    closeshow() {
      this.$store.dispatch('closeshow')
    }
  },
}
</script>
<style lang="less" rel="stylesheet/less" type="text/less" scoped>
.detail {
  box-sizing: border-box;
  position: fixed;
  z-index: 9999;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(7, 17, 27, 0.8); 
  //背景模糊效果
  .background {
    filter: blur(0.1rem);
    background-color: rgba(7, 17, 27, 0.8);
  }
  .sellerinfo {
    position: absolute;
    width: 100%;
    height: 0.32rem;
    top: 1.28rem;
    text-align: center;
    font-size: 0.32rem;
    font-weight: 200;
    color: rgb(255, 255, 255);
    line-height: 0.32rem;
  }
  //星星设置
  .star {
    position: relative;
    width: 100%;
    height: 0.48rem;
    top: 1.92rem;
    text-align: center;
  }
  //商户信息设置
  .lineshow {
    box-sizing: border-box;
    position: relative;
    width: 100%;
    top: 2.64rem;

    .lineone {
      box-sizing: border-box;
      position: relative;
      left: 0.72rem;
      width: 2.24rem;
      height: 0.02rem;
      top: 0.16rem;
      background-color: rgba(255, 255, 255, 0.2)
    }
    p {
      position: relative;
      left: 3.2rem;
      font-size: 0.32rem;
      font-weight: 200;
      color: rgb(255, 255, 255);
      line-height: 0.32rem;
    }
    .linetwo {
      position: absolute;
      right: 0.6rem;
      width: 2.24rem;
      height: 0.02rem;
      top: 0.16rem;
      background-color: rgba(255, 255, 255, 0.2)
    }
    .sellerinfoshow {
      box-sizing: border-box;
      position: relative;
      top: 0.24rem;
      left: 0.72rem;
      width: 100%;
      .infoshowimg {
        display: flex;
        position: relative;
        left: 0.24rem;
        width: 100%;
        margin-top: 0.24rem;
        &>img {
          display: block;
          position: relative;
          width: 0.32rem;
          height: 0.32rem;
          left: 0rem;
        }
        p {
          positive: relative;
          left: 0.12rem;
          font-size: 0.24rem;
          font-weight: 200;
          color: rgb(255, 255, 255);
          line-height: 0.24rem;
        }
      } //商家公告
      .announcement {
        box-sizing: border-box;
        position: relative;
        width: 100%;
        top: 0.56rem;
        left: 0rem;
        .linethree {
          /*display: flex;*/
          position: relative;
          width: 2.24rem;
          height: 0.02rem;
          top: 0.16rem;
          background-color: rgba(255, 255, 255, 0.2)
        }
        p {
          position: relative;
          left: 2.48rem;
          font-size: 0.32rem;
          font-weight: 200;
          color: rgb(255, 255, 255);
          line-height: 0.32rem;
        }
        .linefour {
          position: absolute;
          right: 1.3rem;
          width: 2.24rem;
          height: 0.02rem;
          margin-top: -0.16rem;
          background-color: rgba(255, 255, 255, 0.2)
        }
        .sellerinfoshow {
          box-sizing: border-box;
          display: flex;
          position: relative;
          top: 0.48rem;
          left: 0.24rem;
          width: 94%;
          .infoshowimg {
            box-sizing: border-box;
            position: relative;
            font-size: 0.24rem;
            font-weight: 200;
            width: 80%;
            text-align: left;
            color: rgb(255, 255, 255);
            line-height: 0.48rem;
          }
        }
      }
    }
  }
  //关闭按钮设置
  .closebutton {
    box-sizing: border-box;
    position: absolute;
    bottom: 0.64rem;
    left: 50%;
    width: 0.2rem;
    height: 0.2rem;
    margin-left: -0.2rem;
    font-size: 0.64rem;
    color: rgba(255, 255, 255, 0.5)
  }
}
</style>




