<template>
    <transition enter-active-class="animated  rotateInDownLeft" leave-active-class="animated fadeOutRight"> 
        <!-- 商品详情页 -->
    <div class="detailgoods" ref="detailgoods">
        <div class="total">
              <!-- 头部图像设置 -->
            <div class="foodsimg">
                <img :src="foods.image">
            </div>
               <!-- 头部商品名称等设置 -->
            <div class="headshow">
                <div class="foodsname">{{foods.name}}</div>
                <p class="sellCount">月售{{foods.sellCount}}份</p>
                <p class="rating">好评率{{foods.rating}}%</p>
                <p class="price">￥{{foods.price}}</p>
                <p v-show="foods.oldprice" class="oldprice">￥{{foods.oldprice}}</p>
                <div class="shopping">
                    <add :foods="foods" v-show="foods.count"></add>
                    <div class="shoppingcar" v-show="!foods.count" @click.stop.prevent="addShopping">
                        <p> 加入购物车</p>
                    </div>
                </div>
            </div>
            <div class="headtwo">
                  <!-- 线条引入 -->
                <Lineshow></Lineshow>
            </div> 
            <!-- 商品介绍 -->
            <div class="info">
                <p class="sellerinfo">商品介绍</p>
                <p class="sellerfoods"> {{foods.info}}</p>
            </div>
              <!-- 线条引入 -->
            <Lineshow></Lineshow>
               <!-- 商品评价 -->
            <p class="sellerratings">商品评价</p>
              <!-- 评价详情 -->
            <ratingdetail :ratings="foods.ratings"></ratingdetail>
             <!-- 关闭按钮 -->
            <div @click="closeinfo" class="closebutton"><i class="icon iconfont icon-shiliangzhinengduixiang4"></i></div>
        </div>
    </div>
     </transition> 
</template>
<script>
import Vue from 'vue'
import ratingdetail from '../ratingDetail/ratingdetail'
import Lineshow from '../Line/line.vue'
import add from '../add/add'
import Star from '../star/star'
import { mapGetters } from 'vuex'
export default {

    computed: {
        ...mapGetters([
            'goodsdetailflag',
            'seller',
            'foods',
            'ratings',
        ]),
    },
    components: {
        Star,
        Lineshow,
        ratingdetail,
        add
    },
    methods: {
        //关闭事件
        closeinfo() {
            this.$store.dispatch('closeinfo');
        },
           //控制加减和购物车出现，没有添加商品count属性
        addShopping() {
            if (!this.foods.count) {
                Vue.set(this.foods, 'count', 1)
            } else {
                this.foods.count++;
            }
        },
        //滑动
        _scroll(f) {
            this.$nextTick(() => {
                setTimeout(() => {
                    let scroll = new BScroll(this.$refs[f], {
                        probeType: 3,
                        click: true
                    })
                }, 20)
            })
        }
    },
    //调用滑动
    mounted() {
        this._scroll("detailgoods")
    }
}
</script>
<style lang="less" rel="stylesheet/less" type="text/less" scoped>
.detailgoods {
    box-sizing: border-box;
    position: fixed;
    z-index: 10;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: white;
    .total {
         position: relative;
         //头部图片设置
        .foodsimg {
            width: 100%; 
            img {
                width: 100%;
                height: 100%;
            }
        }
            //头部商品详情设置
        .headshow {
            width: 100%;
            text-align: left;
            padding-bottom: 0.36rem;
            .foodsname {
                margin-left: 0.36rem;
                font-size: 0.28rem;
                font-weight: 700; 
                color: rgb(7, 17, 27);
                line-height: 0.28rem;
            }
            .sellCount {
                // position: relative;
                margin-left: 0.36rem;
                margin-top: 0.16rem;
                font-size: 0.2rem;
                color: rgb(147, 153, 159);
                line-height: 0.2rem;
            }
            .rating {
                // position: relative;
                margin-left: 1.7rem; 
                margin-top: -0.16rem;
                font-size: 0.2rem;
                color: rgb(147, 153, 159);
                line-height: 0.2rem;
            }
            .price {
                // position: relative;
                margin-left: 0.36rem;
                margin-top: 0.36rem;
                font-size: 0.28rem;
                font-weight: 700;
                font-family: normal;
                color: rgb(240, 20, 20);
                line-height: 0.48rem;
            }
            .oldprice {
                // position: relative;
                margin-left: 0.36rem;
                margin-top: -0.4rem;
                margin-left: 1.56rem;
                font-size: 0.20rem;
                font-weight: 700;
                font-family: normal;
                color: rgb(147, 153, 159);
                line-height: 0.48rem;
            }
              //购物车设置
            .shopping {
                position: absolute;
                width: 1.58rem;
                height: 0.59rem;
                margin-left:5.5rem;
                margin-top:-0.5rem;
                .details {
                    position: absolute;
                    top: 0.28rem;
                }
                .shoppingcar {
                    position: absolute;
                    width: 100%;
                    height: 100%;
                    line-height: 0.28rem;
                    border-radius: 0.24rem;
                    background-color: rgb(0, 160, 220);
                    font-size: 0.2rem;
                    color: rgb(255, 255, 255);
                    p {
                        line-height: 0.59rem;
                        text-align: center;
                    }
                }
            }
        }
        #line {
            // position: relative;
            // margin-top: 1.5rem;
        }
          //商户详情设置
        .info {
            margin: 0.36rem;
            text-align: left;
            .sellerinfo {
                font-size: 0.28rem;
                font-weight: 700;
                color: rgb(77, 85, 93);
                line-height: 0.48rem;
                text-align: left;
            }
            .sellerfoods {
                font-size: 0.20rem;
                font-weight: 200;
                color: rgb(77, 85, 93);
                line-height: 0.48rem;
                text-align: left;
            }
        }
        .headtwo {
            #line {
                position: relative; 
            }
        }
        .sellerratings {
            text-align:left;
            position: relative;
            margin: 0.36rem;
        }
        .ratingDetail{
            position: relative;
        }
          //关闭按钮
        .closebutton {
            position: absolute;
           top: 0.1rem;
            left: 0.1rem;
            width: 2rem;
            height: 2rem;
            margin-left: -0.2rem;
            font-size: 0.64rem;
            .iconfont{
                color:red;
                font-size: 0.5rem;
            }
        }
    }
}
</style>




