<template>
  <div>
    <div class="seller" ref="seller">
      <!-- 商家具体详情 -->
      <div class="detailHead">
        <!-- 头部设置 -->
        <div class="top">
          <p class="selelrName">{{seller.name}}</p>
          <star :score="seller.score" ref="serviceScore"> </star>
          <p class="sellphp">({{seller.ratingCount}})</p>
          <p class="sellCount">月售{{seller.sellCount}}份</p>
          <div class="img" @click="show">
            <i v-show="showflag" class="icon iconfont icon-xin whitecolor"></i>
            <i v-show="!showflag" class="icon iconfont icon-xin1 redcolor"></i>
          </div>
          <div class="getfout" @click="show">
            <p v-show="showflag">收藏</p>
            <p v-show="!showflag">已收藏</p>
          </div>
        </div>
      <!-- 配送详情设置 -->
        <div class="bottom">
          <div class="title">
            <p>起送价</p>
            <p>商家配送</p>
            <p>平均配送时间</p>
          </div>
          <div class="num">
            <p>{{seller.minPrice}}</p>元
            <p>{{seller.deliveryPrice}}</p>元
            <p>{{seller.deliveryTime}}</p>分钟
          </div>
        </div>
        <Lineshow></Lineshow>
     <!-- 商家公告 -->
        <div class="detailfoodsShow">
          <div class="sellerhbuild">
            <p>商家公告</p>
            {{seller.bulletin}}
          </div>
          <!-- 商家优惠政策 -->
          <div class="infoshowimg" v-for="(v,k) in (seller.supports)" :key="k">
            <img v-show="v.type==0" src="../../../static/images/decrease.png">
            <img v-show="v.type==1" src="../../../static/images/discount.png">
            <img v-show="v.type==2" src="../../../static/images/guarantee.png">
            <img v-show="v.type==3" src="../../../static/images/invoice.png">
            <img v-show="v.type==4" src="../../../static/images/special.png">
            <p>{{v.description}}</p>
          </div>
          <Lineshow></Lineshow>
            <!-- 商家店面图片 -->
          <div class="sellshopping">
            <p>商家实景</p>
            <div class="sellshoppingImg" ref="sellshoppingImg">
              <div class="addscroll">
                <img v-for="(v,i) in seller.pics" :key="i" :src="v">
              </div>
            </div>
          </div>
          <div class="lastline">
            <Lineshow></Lineshow>
          </div>
   <!-- 商家信息联系方式 -->
