<script>
import shopping from '../shopping/shopping'
import Lineshow from '../Line/line.vue'
import Star from '../star/star'
import { mapGetters } from 'vuex';
export default {
  data() {
    return {
      showflag: true
    }
  },
  computed: {
    ...mapGetters([
      'seller',
      'goods'
    ])

  },
  components: {
    Star,
    Lineshow,
    shopping
  },
  methods: {
    //星星大小设置
    _getStarsecs(h, s, f) {
      setTimeout(() => {
        let m = this.$refs[f].$el;
        if (m.children) {
          for (let i = 0; i < m.children.length; i++) {
            m.children[i].style.width = h + "rem";
            m.children[i].style.height = s + "rem"
            m.children[i].style.margin = 0.05 + "rem"
          }
        }

      }, 30)
    },
    //滑动
    _scrolls(f,m) {
      setTimeout(() => {
        this.scrollm = new BScroll(this.$refs[f], {
          probeType: 3,
          click: true,
         scrollX: true
        })
      }, 50)

    },
//点击收藏和未收藏设置
    show() {
      this.showflag = !this.showflag
    }
  },
  mounted() {
    this.$store.dispatch('getSeller');
    this._getStarsecs(0.36, 0.36, "serviceScore");
    this._scrolls("seller");
    this._scrolls("sellshoppingImg",true);

  }
}
</script>
