<template>
  <div>
    <div class="goods">
      <!-- 左边栏设置 -->
      <div class="goods-menu" id="goods" ref="goods">
        <div>
          <div @click="selectMenu(k,$event)" v-for="(v,k) in goods" :key="k" class="menuShow" :class="{'current':k===currentIndex}">
  
            <div class="line"></div>
            <div class="goodsName">{{v.name}}</div>
          </div>
        </div>
      </div>
        <!-- 右边栏设置 -->
      <div class="detail-menu" id="foods" ref="foodser">
        <div>
            <!-- 循环遍历具体每种商品 -->
          <div v-for="(v,k) in goods" :key="k" class="detailMenushow food-list-hook">
            <div class="lineHead">
              <div class="line"></div>
              <div class="goodsName">
                {{v.name}}</div>
            </div>
              <!-- 再循环遍历具体每种商品的具体信息 -->
            <div  v-for="(value,key) in v.foods" :key="key" class="detail"  @click.stop="openinfo(value,k,$event)">
              <img :src="value.image" alt=""   >
              <p class="goodName">{{value.name}}</p>
              <p class="description">{{value.description}}</p>
              <div class="sellCount">
                <p class="sell">月售{{value.sellCount}}份</p>
                <p class="rating">好评率{{value.rating}}%</p>
              </div>
              <p class="price">￥{{value.price}}</p>
              <p v-if="value.oldPrice" class="oldPrice">￥{{value.oldPrice}}</p>
              <add :foods="value"></add>
            </div>
          </div>
        </div>
      </div>
        <!-- 引用商品具体详情组件 -->
      <Goodsdetil v-if="goodsdetailflag"></Goodsdetil>
    </div>
      <!-- <shopping :goods="goods" v-if="goods.length>0"></shopping>  -->
    <!-- <shopping></shopping> -->
  
  </div>
</template>
<script>

import shopping from '../shopping/shopping'
import add from '../add/add'
import { mapGetters } from 'vuex'
import Goodsdetil from '../goodsdetail/goodsdetail'
export default {
  data() {
    return {
      listHeight: [],
      scorllY: 0,
      count: 0
    }
  },
  computed: {
    ...mapGetters([
      'goods',
      'foods',
      'goodsdetailflag'
    ]),
    //获取具体滑动商品所对应的菜单栏的i值，找到具体的分类，默认0
    currentIndex() {
      for (let i = 0; i < this.listHeight.length; i++) {
        let height1 = this.listHeight[i];
        let height2 = this.listHeight[i + 1];
        if (this.scorllY >= height1 && this.scorllY < height2) {
          return i
        }
      }
      return 0
    }
  },
  components: {
    Goodsdetil,
    add,
    shopping
  },
  methods: {
    //打开具体商品详情，传递数据
    openinfo(val,k,e) {
      if (!e._constructed) {
        return;
      }
      this.$store.dispatch('openinfos',{val,k});
    },
    //点击左侧的导航，右侧商品滑到具体的分类
    selectMenu(key, e) {
      if (!e._constructed) {
        return;
      }
      let foodList = this.$refs.foodser.getElementsByClassName("food-list-hook");
      let el = foodList[key];
      this.scrollm.scrollToElement(el, 300);
    },
    //左侧滑动事件
    _scroll(f) {
      this.scroller = new BScroll(this.$refs[f], {
        probeType: 3,
        click: true
      })
    },
     //右侧滑动事件
    _scrolls(f) {
      this.scrollm = new BScroll(this.$refs[f], {
        probeType: 3,
        click: true
      })
      this.scrollm.on('scroll', (pos) => {
        this.scorllY = Math.abs(Math.round(pos.y));
      })
    },
    //计算每一种类商品的高
    _calcuHeight() {
      let foodList = this.$refs.foodser.getElementsByClassName("food-list-hook");
      let height = 0;
      this.listHeight.push(height);
      for (let i = 0; i < foodList.length; i++) {
        height += foodList[i].clientHeight;
        this.listHeight.push(height);
      }
    }
  },
  //发送执行事件
  created() {
    this.$store.dispatch('getGoods');
    this.$nextTick(() => {
      setTimeout(() => {
        this._scrolls("foodser");
        this._scroll("goods");
        this._calcuHeight();
      }, 30)
    })
  }
}
</script>
<style lang="less" rel="stylesheet/less" type="text/less" scoped>
.goods {
  z-index: 1;
  box-sizing: border-box;
  position: absolute;
  top: 3.48rem;
  width: 100%;
  height: 65%;
  left: 0;
  display: flex;
  overflow: hidden;
  //左侧导航设置
  .goods-menu {
    z-index: 1;
    position: relative;
    height: 100%;
    flex: 0 0 1.6rem;
    overflow: hidden;
    .menuShow {
      &.current {
        background: white;
      }
      padding: 0.24rem;
      box-sizing: border-box;
      width: 100%;
      height: 1.08rem;
      background: #f3f5f7;
      text-align: center;
      line-height: 0.24rem;
      .goodsName {
        height: 0.64rem;
        font-size: 0.24rem;
        color: black;
        width: 100%;
        font-weight: 200;
      }
      .line {
        position: relative;
        top: 0.8rem;
        width: 1.12rem;
        height: 0.01rem;
        background-color: rgb(7, 17, 27, 0.1);
      }
    }
  }
    //右侧导航设置，具体详情商品布置
  .detail-menu {
    z-index: 1;
    width: 5.9rem;
    position: relative;
    height: 100%;
    overflow: hidden;
    .detailMenushow {
      position: relative;
      width: 100%;
      .lineHead {
        width: 100%;
        height: 0.52rem;
        .line {
          position: relative;
          float: left;
          width: 0.04rem;
          height: 0.52rem;
          background-color: #d9dde1;
          /*display: inline-block;*/
        }
        .goodsName {
          position: relative;
          display: inline-block;
          float: right;
          box-sizing: border-box;
          height: 0.52rem;
          width: calc(100% - 0.68rem);
          font-size: 0.24rem;
          color: rgb(147, 153, 159);
          line-height: 0.52rem;
          background-color: #f3f5f7;
        }
      }
  //具体商品设置
      .detail {
        position: relative;
        width: 100%;
        height: 1.42rem;
        padding: 0.36rem;
        img {
          position: absolute;
          top: 0.36rem;
          left: 0.36rem;
          width: 1.2rem;
          height: 1.2rem;
        }
        .goodName {
          position: absolute;
          top: 0.4rem;
          left: 1.76rem;
          font-size: 0.28rem;
          color: rgb(147, 153, 159);
          line-height: 0.2rem;
        }
        .description {
          position: absolute;
          top: 0.84rem;
          left: 1.76rem;
          font-size: 0.2rem;
          color: rgb(147, 153, 159);
          line-height: 0.28rem;
          white-space: nowrap;
          overflow: hidden;
        }
        .sellCount {
          position: absolute;
          top: 1.2rem;
          left: 1.76rem;
          font-size: 0.2rem;
          color: rgb(147, 153, 159);
          line-height: 0.28rem;
          .sell {
            display: inline;
            position: relative;
            left: 0;
            top: 0;
          }
          .rating {
            display: inline;
            position: relative;
            left: 0.24rem;
            top: 0;
          }
        }
        .price {
          position: absolute;
          bottom: 0rem;
          left: 1.76rem;
          font-size: 0.28rem;
          color: red;
          font-weight: 200;
          font-family: normal;
          line-height: 0.48rem;
        }
        .oldPrice {
          position: absolute;
          bottom: 0rem;
          left: 2.48rem;
          font-size: 0.2rem;
          color: rgb(147, 153, 159);
          font-weight: 200;
          font-family: normal;
          text-decoration: underline;
          line-height: 0.48rem;
        }
          //加减号的位置设置
        .details{
           position: absolute;
           bottom:0;
           right:0.56rem;
        }
      }
    }
  }
}
</style>




