<template>
 <transition enter-active-class="animated  zoomInUp" leave-active-class="animated zoomOutDown"> 
    <div id="shoppingdetail" v-show="detailflags">   
        <div class="car">
             <!-- 购物车标题 -->
            <div class="shopping">购物车</div>
            <div class="clean" @click="clearfoods">清空</div>
        </div>
         <!-- 购物车详情 -->
        <div class="shoppingdetailfoods" ref="shoppingdetail">
                <div>
                    <div class="detailfoods" v-for="(v,k) in selectFoods" :key="k">
                        <div class="everyfoods">
                            <p>{{v.name}}</p>
                        </div>
                        <div class="everyprice">
                            <p>{{v.price}}</p>
                        </div>
                        <add :foods="v"></add>
                    </div>
                </div>
        </div>
    </div>
    </transition >
</template>

<script>
import { mapGetters } from 'vuex'
import add from '../add/add'
import Vue from 'vue'
export default {
    data() {
        return {
            detailfoods: ""
        }
    },
    components: {
        add
    },
    computed: {
        ...mapGetters([
            "goods",
            'ratings',
            'seller',
            "foods",
            'detailflags'
        ]),
        //获取加入购物车的数据加入数组
        selectFoods() {
            let  selectFoods=[];
            for(let i=0;i<this.goods.length;i++){
                    this.goods[i].foods.forEach((food)=>{
                        if(food.count){
                             selectFoods.push(food)
                        }
                    })
            }
              return selectFoods
        },
    },
    methods: {
        //滚动
        _scroll() {
            if(!this.scroll){
                this.scroll = new BScroll(this.$refs.shoppingdetail, {
                    click: true
                })
            }else{
                this.scroll.refresh()
            }
            
        },
        //清除购物车数据
        clearfoods() {
             for (let i = 0; i < this.goods.length; i++) {
                this.goods[i].foods.forEach(food => {
                    if (food.count && food.count > 0) {
                        Vue.delete(food, 'count');
                    }
                })
            }                      
        }
    },
    //调用滚事件
    updated() {
        setTimeout(()=>{
            this._scroll()
        },20)
        
    }
}
</script>
<style lang="less" rel="stylesheet/less" type="text/less" scoped>
#shoppingdetail {
    width: 7.5rem;
    bottom: 0rem; // max-height: 6.11rem;  
    z-index: 88;
    bottom: 0;
    left: 0;
    right: 0;
    background:#ffffff;
    position: fixed;
    margin-bottom: 1.16rem;
    //头部标题设置
    .car {
        display: flex;
        width: 100%;
        height: 0.8rem;
        background: #f3f5f7;
        padding: 0 0.36rem 0 0.36rem;
        text-align: left;
        .shopping {
            flex: 1;
            font-size: 0.28rem;
            font-weight: 200;
            color: rgb(7, 17, 27);
            line-height: 0.8rem;
        }
        .clean {
            flex: 1;
            text-align: center;
            font-size: 0.24rem;
            color: rgb(0, 160, 220);
            line-height: 0.8rem;
        }
    }
    //具体内容布局添加滑动
    .shoppingdetailfoods {
        width: 100%;
        max-height: 4.15rem;
        overflow: hidden;
        .detailfoods {
            display: flex;
            position: relative;
            width: 100%;
            height: 0.96rem;
            border-top: 0.01rem solid #ccc;
            border-bottom: 0.01rem solid #ccc;
            //购物车商品名称
            .everyfoods {
                position: absolute;
                margin-left: 0.36rem;
                font-size: 0.28rem;
                color: rgb(7, 17, 27);
                line-height: 0.96rem;
                white-space: nowrap;
            }
              //购物车商品价格
            .everyprice {
                position: absolute;
                line-height: 0.96rem;
                margin-left: 4rem;
                font-size: 0.28rem;
                font-weight: 700;
                font-family: normal;
                color: rgb(240, 20, 20);
            }
              //购物车商品添加add组件
            .details {
                position: absolute;
                right: 0rem;
                line-height: 0.96rem;
                top: 0.4rem;
            }
        }
    }
}
</style>



