// import * as types from "../types"
import types from "../types"
import axios from 'axios'
//相当于vue中data
const state = {
  ratings: null,


}
//返回数据
const getters = {
  ratings(state) {
    return state.ratings
  },

}
//接收components传递的方法和数据交给mutations执行
const actions = {
  getRatings({ commit, state }) {
    axios.get('../../../static/data.json')
      .then(resp => {
        if (resp.data.erron == 0) {
          commit(types.GET_RATINGS, resp.data.ratings)
        }
      })
  },
  
}
//执行具体的操作
const mutations = {
  [types.GET_RATINGS](state, data) {
    state.ratings = data
  },
}
//输出数据
export default {
  state,
  getters,
  actions,
  mutations
}



