<template>
     <transition  name="fade"> 
         <!-- 加减组件 -->
    <div class="details">
        <div class="countNum">
            <div v-show="foods.count" @click.stop.prevent="decrement($event)" class="remove">-</div>
            <div v-show="foods.count" class="count">{{foods.count}}</div>
            <div @click.stop.prevent="increament($event)" class="add"> +</i>
            </div>
        </div>
    </div>
     </transition> 
</template>

<script>
import Vue from 'vue'
import Star from '../star/star'
import { mapGetters } from 'vuex'
export default {
    data() {
        return {

        }
    },
    computed: {
         //获取数据
        ...mapGetters([
            'showdetailflag',
            'seller',

        ])

    },
    props: ['foods'],
    //插入组件
    components: {
        Star
    },
    methods: {
      //加法事件
        increament(e) {
              if (!e._constructed) {
                  return;
              }
            if (!this.foods.count) {
                Vue.set(this.foods, 'count', 1)
            } else {
                this.foods.count++;
            }
        },
         //减法事件
        decrement(e) {
              if (!e._constructed) {
               return;
                 }
            this.foods.count--;
            if (this.foods.count == 0) {
                Vue.delete(this.foods, 'count');
            }
        }
    },
}
</script>
<style lang="less" rel="stylesheet/less" type="text/less" scoped>
.fade-enter-active, .fade-leave-active {
  transition: opacity 1s
}
.fade-enter, .fade-leave-to {
  opacity: 0
}
.details {
    .countNum {
        position: relative;
        display: flex;
        width: 2rem;
        bottom: 0.3rem;
        height: 0.5rem;

             //减号
        .remove {
            position: absolute;
            width: 0.5rem;
            height: 0.5rem;
            color: wheat;
            border-radius: 50%; // border:0.01rem solid  #00a0dc;
            background-color: #00a0dc;
            text-align: center;
            line-height: 0.5rem;
            margin: 0.1rem;
            font-weight: 700;
            font-size: 0.3rem;
        }
         //加号
        .add {
            position: absolute;
            left: 1.1rem;
            width: 0.5rem;
            height: 0.5rem;
            border-radius: 50%;
            background-color: #00a0dc;
            color: wheat;
            text-align: center;
            line-height: 0.5rem;
            margin: 0.1rem;
            font-weight: 700;
            font-size: 0.5rem;
        }
         //数字
        .count {
            height: 0.5rem;
            width: 0.5rem;
            position: absolute;
            left: 0.6rem;
            line-height: 0.5rem;
            margin: 0.1rem;
            font-weight: 200;
            text-align: center;
        }
    }
}
</style>



