<template>
    <div class="header">
      <!-- 头部设置 -->
        <div class="background">
            <img :src="seller.avatar" class="backgroundImg">
        </div>
          <!-- 头部商品详情设置 份三部分-->
        <div class="header-tip">
            <div class="header-first" @click="showdetail">
                <img :src="seller.avatar" class="avatar">
            </div>
            <div class="header-second">
                <div class="header-img" >
                    <img src="../../../static/images/brand.png" class="brand">
                </div>
                <div class="title">{{seller.name}}</div>
                <div class="delivery">{{seller.description}}/{{seller.deliveryTime}}分钟送达</div>
                <div class="header-news">
                    <img src="../../../static/images/decrease.png" class="newsimg">
                    <span v-if="seller.supports">{{seller.supports[0].description}}</span>

                </div>

            </div>
            <div class="header-third" @click="showdetail">
                <span v-if="seller.supports">{{seller.supports.length}}个&nbsp;&nbsp;&nbsp;></span>
            </div>
        </div>
          <!-- 商家介绍详情设置 -->
        <div class="header-last">
            <img src="../../../static/images/bulletin.png">
            <div class="lastspan" v-if="seller.supports">{{seller.bulletin}}</div>
            <div class="lastimg">></div>
        </div>
        <Detail></Detail>
    </div>
</template>
<script>
import { mapGetters } from 'vuex';
import Detail from '../Detail/show.vue'
export default {
    //获取商家数据
    computed: {
        ...mapGetters([
            'seller',
             'showdetailflag'

        ])
    },
    components:{
        Detail
    },
    //点击显示详情页方式
    methods: {
        showdetail() {
            this.$store.dispatch('show')
        }
    },
    //默认获取数据
    created() {
        this.$store.dispatch('getSeller');
    }
}

</script>
