<template>
    <div>
        <!-- <transition-group enter-active-class="animated  fadeInLeft" leave-active-class="animated fadeOutRight"> -->
    
        <div class="ratingDetail">
            <!-- 评价详情 -->
            <div class="button">
                <!-- 三个按钮设置 -->
                <div class="block positive" :class = "{ 'active' :  selectType == 2}" @click="select(2,$event)">全部
                    <p class="count">{{ratings.length}}</p>
                </div>
                <div class="block positive" :class = "{ 'active' :  selectType == 0}" @click="select(0,$event)">满意
                    <p class="count">{{postive.length}}</p>
                </div>
                <div class="block negative" :class = "{ 'active' :  selectType == 1}"  @click="select(1,$event)">不满意
                    <p class="count">{{negative.length}}</p>
                </div>
            </div>
             <!-- 内容选择设置 -->
            <div class="checkShow">
               <div  @click="checktest" class="inconimg" :class="{'active':onlycontext==true}">
                     <i class="icon iconfont icon-duihao"></i>
               </div>           
                只能看到有内容的评价
            </div>
             <!-- 评价具体设计 -->
            <div id="ratingDetail">
                <div v-for="(v,k) in show" :key="k" class="show">
                    <img :src="v.avatar" class="img">
                    <div class="username">{{v.username}}</div>
                    <star :score="v.score" ref="ratingshowimg"> </star>
                    <div class="deliveryTime">{{v.deliveryTime}} 分钟送达</div>
                    <div class="rateTime">{{_showNowDay(v.rateTime)}}</div>
                    <div class="text">
                        <p>{{v.text}}</p>
                    </div>
                      <!-- 推荐 -->
                    <div class="recommend">
                        <div class="typerecomd" v-show="v.rateType">
                            <i class="icon iconfont icon-down"></i>
                            {{v.rateType}}</div>
                        <div class="type" v-show="!v.rateType">
                            <i class="icon iconfont icon-damuzhion"></i>{{v.rateType}}</div>
                        <div class="recommendshow" v-for="(value,i) in v.recommend" :key="i">
                            <div class="recommdfood">
                                {{value}}...</div>
                        </div>
                    </div>
    
                </div>
            </div>
        </div>
    </div>
    <!-- </transition-group> -->
</template>
</div>
<script>
import Star from '../star/star'
import { mapGetters } from 'vuex'
export default {
    data() {
        return {
            count: 0,
            comTime: '',
            onlycontext: false,
            show: [],
            selectType: 2
        }
    },
    props: ["ratings"],
    computed: {
        ...mapGetters([
            'showdetailflag',
            'seller',
        ]),
        scorestar() {
            return this.seller.score
        },
      //好评的详情计算属性
        postive() {
            if (this.ratings) {
                return this.ratings.filter((v) => {
                    return v.rateType == 0
                })
            }
        },
        //差评的详情计算属性
        negative() {
            if (this.ratings) {
                return this.ratings.filter((v) => {
                    return v.rateType == 1
                })
            }
        },
    },
    components: {
        Star
    },
    methods: {
        //点击满意不满意和显示内容都会调用的方式
        _showdetail() {
               this.$emit("refresh")
               this._getStarsec(0.2, 0.2, "ratingshowimg");
            if (this.selectType == 2) {
                if (!this.onlycontext) {
                  return  this.show = this.ratings
                }
                this.show = this.ratings.filter((v) => {
                    return v.text
                })
            } else if (this.selectType == 0) {
                if (!this.onlycontext) {
                   return this.show = this.ratings.filter((v) => {
                        return v.rateType == 0
                    })
                }
                this.show = this.ratings.filter((v) => {
                    return v.rateType == 0 &&  v.text
                })
            } else {
                if (!this.onlycontext) {
                   return this.show = this.ratings.filter((v) => {
                        return v.rateType == 1
                    })
                }
                this.show = this.ratings.filter((v) => {
                    return v.rateType == 1 &&  v.text
                })
            }
        },
        //点击内容显示事件
        checktest() {
            this.onlycontext = !this.onlycontext;
            this._showdetail()
        },
        //点击满意不满意事件
        select(k, e) {
            if (!e._constructed) {
                return;
            };
            this.selectType = k;
            this._showdetail()
        },
        //滑动
        _scroll(f) {
            this.$nextTick(() => {
                setTimeout(() => {
                  this.scroll = new BScroll(this.$refs[f], {
                        probeType: 3,
                        click: true
                    })
                }, 30)
            })
        },
        //时间戳转换成当前时间
        _showNowDay(m) {
            let t = new Date(m)
            function zero(x) {
                return x > 10 ? x : "0" + x
            }
            return t.getFullYear() + "-" + t.getMonth() + "-" + t.getDay() + "  " + t.getHours() + ":" + zero(t.getMinutes()) + ":" + zero(t.getSeconds())
        },
        //设置星星的大小
        _getStarsec(h, s, f) {
            setTimeout(() => {
                if (this.$refs[f]) {
                    this.$refs[f].forEach((v, i) => {
                        for (let i = 0; i < v.$el.children.length; i++) {
                            v.$el.children[i].style.width = h + "rem";
                            v.$el.children[i].style.height = s + "rem";
                            v.$el.children[i].style.margin = 0.01 + "rem";
                        }
                    })
                }

            }, 30)

        },
    },
    mounted() {
            this._getStarsec(0.2, 0.2, "ratingshowimg");
            this._showdetail();
    }
}
</script>
<style lang="less" rel="stylesheet/less" type="text/less" scoped>
.ratingDetail {
    box-sizing: border-box;
    position: relative;
    margin-top: 0.8rem;
    width: 100%;
    margin: 0.8rem 0 0.06rem 0;
    //三个按钮的设置
    .button {
        display: flex;
        position: relative;
        margin-left: 0.36rem;
        text-align: center;
        .block {
            display: flex;
            height: 0.56rem;
            text-align: center;
            line-height: 0.5rem;
            padding: 0.16rem 0.24rem;
            margin-right: 0.16rem;
            border-radius: 0.02rem;
            color: rgb(77, 85, 93);
            font-size: 0.24rem;
             background: rgba(0, 160, 220, 0.2);
            // background: rgba(0, 160, 220, 0.2);
            &.active {
                color: #fff;
            }
            .count {
                text-align: center;
                margin-left: 0.04rem;
                font-size: 0.16rem;
            }
        
        &.positive {
            background: rgba(0, 160, 220, 0.2);
            &.active {
                background: rgb(0, 160, 220);
            }
        }
        &.negative {
            background: rgba(77, 85, 93, 0.2);
            &.active {
                background: rgb(77, 85, 93);
            }
        }
    }
    }
       //点击内容的设置
    .checkShow {
        width: 100%;
        width: 6.7rem;
        display: flex; // height: 0.72rem;
        border-top: 0.02rem solid rgba(7, 17, 27, 0.2);
        border-bottom: 0.02rem solid rgba(7, 17, 27, 0.2);
        margin: 0.36rem 0 0 0.4rem;
        font-size: 0.24rem;
        color: rgb(147, 153, 159); // line-height:0.24rem;
        .inconimg{
                &.active{
                    color:red;
                }
        }
        .iconfont {
            margin-right: 0.1rem;
        }
    }
}
   //商品评价详情
#ratingDetail {
    width: 100%; // display: flex;
    .show {
        width: 100%;
        position: relative;
        img {
            position: absolute;
            left: 0.36rem;
            top: 0.36rem;
            width: 0.56rem;
            height: 0.56rem;
            border-radius: 50%;
        }
        .username {
            position: absolute;
            top: 0.36rem;
            left: 1.16rem;
            font-size: 0.2rem;
            color: rgb(7, 17, 27);
            line-height: 0.24rem;
        }
        .star {
            position: absolute;
            top: 0.32rem;
            left: 1.16rem;
            font-size: 0.2rem;
        }
        .deliveryTime {
            position: absolute;
            top: 0.55rem;
            left: 2.33rem;
            font-size: 0.2rem;
            font-weight: 200;
            color: rgb(147, 153, 159);
            line-height: 0.24rem;
        }
        .rateTime {
            display: block;
            position: absolute;
            top: 0.36rem;
            right: 0.36rem;
        }
        .text {
            position: relative;
            margin: 0 0.36rem 0.12rem 0.36rem;
            padding: 1.04rem 0.36rem 0.12rem 0.8rem;
            p {
                text-align: left;
                font-size: 0.24rem;
                color: rgb(7, 17, 27);
                line-height: 0.36rem;
            }
        }
        .recommend {
            position: relative;
            font-size: 0;
            display: flex;
            width: 5.2rem;
            overflow: hidden;
            margin-left: 0.76rem;
            margin-top: 0.12rem; // display: inline-block;
            bottom: 0.36rem;
            .type {
                display: flex;
            }
            .typerecomd {
                display: flex;
            }
            .recommendshow {
                // overflow:hidden
                // display:  inline-block;
                .recommdfood {
                    border: 0.01rem solid rgb(7, 17, 27, 0.1);
                    border-radius: 0.02rem;
                    background: rgb(255, 255, 255);
                    font-size: 0.18rem;
                    color: rgb(147, 153, 159);
                    line-height: 0.32rem;
                    overflow: hidden;
                    white-space: nowrap;
                    padding: 0.02rem 0.05rem;
                    margin: 0 0.16rem;
                }
            }
        }
    }
}
</style>



