// import * as types from "../types"
import types from "../types"
import axios from 'axios'
//相当于vue中data
const state = {
    seller: {},
    showdetailflag: false
}
//返回数据
const getters = {
    seller(state) {
        return state.seller
    },
 showdetailflag(state) {
        return state.showdetailflag
    }
}
//接收components传递的方法和数据交给mutations执行
const actions = {
    getSeller({ commit, state }) {
        axios.get('../../../static/data.json')
            .then(resp => {
                if (resp.data.erron == 0) {
                    commit(types.SELLER_ACTION, resp.data.seller)
                }
            })
    },
    closeshow({ commit }) {
        commit(types.CLOSE_SHOW)

    },
    show({ commit }) {
        commit(types.DETAIL_SHOW)
        
    }
}
//执行具体的操作
const mutations = {
    [types.SELLER_ACTION](state, data) {
        state.seller = data
    },
    [types.CLOSE_SHOW](state) {
        state.showdetailflag = false
    },
    [types.DETAIL_SHOW](state) {
        state.showdetailflag = true
    }

}
//输出数据
export default {
    state,
    getters,
    actions,
    mutations
}


