<template>
<div class="star">
 <img v-show="score>=k+1" src="../../../static/images/star_on.png" v-for="(v,k) in 5" :key="k"  class="score">
  <img v-show="score*10%10>5" src="../../../static/images/star_half.png"  class="score" >
   <img  v-show="Math.round(score)<k+1" src="../../../static/images/star_off.png"   v-for="(v,k) in 5" :key="k" class="score" >
  </div>
</template>
<script>
    export default{
       props:["score"]

    }

</script>
<style lang='less' rel="stylesheet/less" type="text/less" scoped>
  .star{
    img {
      width: 0.5rem;
      height: 0.5rem;
      margin-left:0.2rem;
    }

  }


</style>




