<template>
  <div class="shopping">
    <!-- 底部购物车 -->
    <div class="car" @click="detailflag" >
      <i class="icon iconfont icon-gouwuche"></i>
    </div>
    <div v-if="totalcount>0" class="num">{{totalcount}}</div>
    <div class="price">
      ￥
      <p>{{totalPrice}}</p>
    </div>
    <div class="sellprice">
      另需配送费
      <p>{{seller.deliveryPrice}}</p>元
    </div>
    <div class="sendprice" @click="showpay">
      <div class="pay" :class='{"enough":totalPrice>=seller.minPrice}'>{{payDesc}}</div>
    </div>
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
export default {
  props: ["goods"],
  computed: {
    ...mapGetters([
      'seller',
      'detailflags'
    ]),
        //获取被加上count属性的商品
    selectFoods() {
      let foods = [];
      this.goods.forEach((good) => {
        good.foods.forEach(food => {
          if (food.count > 0) {
            foods.push(food);
          }
        })
      })
      return foods
    },
      //获取被加上count属性的商品，计算价格
    totalPrice() {
      let total = 0
      this.selectFoods.forEach(food => {
        total += food.count * food.price;
      })
      return total;
    },
     //获取被加上count属性的商品，计算数量
    totalcount() {
      let count = 0;
      this.selectFoods.forEach(food => {
        count += food.count;
      })
      return count;
    },
    
        //获取被加上count属性的商品，计算可不可结算
    payDesc() {
      if (this.totalPrice === 0) {
        return `￥${this.seller.minPrice}元`
      } else if (this.totalPrice < this.seller.minPrice) {
        return `还差${this.seller.minPrice - this.totalPrice}元起送`
      } else {
        return "去结算"
      }
    }
  },
  methods:{
    //需要支付的金额加运费
    showpay(){
        if(this.totalPrice >= this.seller.minPrice){
       let totalmoney=this. totalPrice+this.seller.deliveryPrice;
        alert(totalmoney) 
        }
       
    },
    //点击显示购物车详情页
    detailflag(){
       this.$store.dispatch('detailflag');
       
    }
  }
  }

</script>
<style lang='less' rel="stylesheet/less" type="text/less" scoped>
.shopping {
  box-sizing: border-box;
  z-index: 99;
  position: absolute;
  bottom: 0rem;
  left: 0rem;
  bottom: 0;
  right: 0;
  width: 7.5rem;
  height: 1.16rem;
  text-align: center;
  background: rgb(0, 0, 0, 0.2);
  //购物车
  .car {
    position: absolute;
    left: 0.36rem;
    background-color: rgb(255, 255, 255, 0.4);
    bottom: 0.16rem;
    border: 0.14rem solid black;
    width: 0.88rem;
    height: 0.88rem;
    border-radius: 50%;

    .icon {
      font-size: 0.48rem;
      line-height: 0.68rem;
    }
  }
  //数量 
  .num {
    position: absolute;
    color: #00b43c;
    width: 0.48rem;
    height: 0.36rem;
    background-color: red;
    left: 0.88rem;
    line-height: 0.32rem;
    font-weight: 700;
    top: -0.24rem;
    border-radius: 24%;
    color: white;
  }
    //价格 
  .price {
    display: flex;
    position: relative;
    top: 0.44rem;
    left: 1.4rem;
    font-size: 0.32rem;
    color: rgb(255, 255, 255, 0.1);
    font-weight: 700;
    line-height: 0.48rem;
  }
  .sellprice {
    display: flex;
    position: relative;
    margin-left:2.5rem;
    padding: 0 0.24rem 0 0.24rem;
    width: 2.18rem;
    height: 0.48rem;
    border-left: 0.01rem solid rgb(255, 255, 255, 0.1);
    border-right: 0.01rem solid rgb(255, 255, 255, 0.1);
    font-size: 0.32rem;
    color: rgb(255, 255, 255, 0.4);
    font-weight: 700;
    line-height: 0.48rem;
  }
  .sendprice {
    position: absolute;
    right: 0;
    top: 0;
    bottom: 0;
    width: 2.44rem;
    height: 9rem; 
    .pay {
      width: 100%;
      height: 100%;
      position: relative;
      margin: 0;
      font-size: 0.24rem;
      font-weight: 700;
      line-height: 1.16rem;
      color: #26333b;
      background-color: #2b343c;
      color: rgba(255, 255, 255, 0.4);
      text-align: center;
      &.not-enough {
        background: #2b333b;
      }
      &.enough {
        background: #00b43c;
        color: #fff;
      }
    }
  }
}
</style>




