<template>
  <div id="app">
    <!-- 引入头部 -->
    <v-header></v-header>
     <!-- 路由 -->
    <div class="target">
      <div class="target-goods">
        <router-link to="/goods">商品</router-link>
      </div>
      <div class="target-ratings">
        <router-link to="/ratings">评价</router-link>
      </div>
      <div class="target-seller">
        <router-link to="/seller">商家</router-link>
      </div>
      <!-- <Lineshow></Lineshow> -->
      <router-view></router-view>
    </div>
  <!-- 购物车 -->
    <shopping :goods="goods" v-if="goods.length>0"></shopping>  
      <!-- 购物车详情 -->
   <detailshopping :goods="goods"></detailshopping>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import detailshopping from './components/datailShopping/detailShopping'
import shopping from './components/shopping/shopping'
import Vue from 'vue'
import Header from './components/header/header.vue'
import Lineshow from './components/Line/line'
import Element from 'element-ui'
import 'element-ui/lib/theme-default/index.css'
Vue.use(Element)
export default {
  name: 'app',
  //组件，引入外来文件需要注册
  components: {
    'v-header': Header,
    Lineshow,
    shopping,
    detailshopping
  },  
  computed: {
    ...mapGetters([
      'ratings',
      'seller',
      'goods',
      'foods'
    ]),
}
}
</script>
<style lang="less" rel="stylesheet/less" type="text/less" scoped>
#app{
  width:100%;
  height:100%;
  top:0;
  bottom:0;
  left:0;
  right:0;
  position: fixed;
.target {
  box-sizing: border-box;
  height: 0.8rem;
  width: 7.5rem;
  display: flex;
  text-align: center;
  line-height: 0.8rem;
  font-size: 0.28rem;
  color: rgb(77, 85, 93);
  font-size: 0.28rem;
  border-bottom: 0.01rem solid rgb(7, 17, 27, 0.1);
  .target-goods {
    flex: 1;
    text-align: center
  }
  .target-ratings {
    flex: 1
  }
  .target-seller {
    flex: 1
  }
  .router-link-exact-active {
    color: rgb(240, 20, 20);
  }
}
}
</style>



