// import * as types from "../types"
import types from "../types"
import axios from 'axios'
//相当于vue中data
const state = {
  goods: {},
  foods: {},
  goodsdetailflag: false,
  detailflags: false
}
//返回数据
const getters = {
  goods(state) {
    return state.goods
  },
  goodsdetailflag(state) {
    return state.goodsdetailflag
  },
  foods(state) {
    return state.foods
  },
  detailflags(state) {
    return state.detailflags
  }
}
//接收components传递的方法和数据交给mutations执行
const actions = {
  getGoods({ commit, state }) {
    axios.get('../../../static/data.json')
      .then(resp => {
        if (resp.data.erron == 0) {
          commit(types.GET_GOODS, resp.data.goods)
        }
      })
  },
  openinfos({ commit, state }, { val, k }) {

    commit(types.OPEN_INFOS, { val, k })
  },
  closeinfo({ commit }) {
    commit(types.CLOSE_INFOS)
  },
  detailflag({ commit }) {
    commit(types.SHOW_SHOPPING)
  }
}
//执行具体的操作
const mutations = {
  [types.GET_GOODS](state, data) {
    state.goods = data
    // console.log( state.goods)
  },
  // [types.OPEN_INFOS](state, val) {
  [types.OPEN_INFOS](state, { val, k }) {
    // state.foods = state.goods[k].foods[key]
    state.foods = val;
    state.goodsdetailflag = true
  },
  [types.CLOSE_INFOS](state) {
    state.goodsdetailflag = false
  },
  [types.SHOW_SHOPPING](state) {
    state.detailflags = !state.detailflags
  }
}
//输出数据
export default {
  state,
  getters,
  actions,
  mutations
}



