<template>
  <div>
    <div class="ratings" ref="ratingDetails">
      <div>
        <!-- 评价详情 头部设置 左右设置-->
        <div class="detailHead">
          <div class="ratingLeft">
            <p class="score">{{seller.score}}</p>
            <p class="totalRatings">综合评分</p>
            <p class="rankRate">高于周边商家{{seller.rankRate}}</p>
          </div>
          <!-- 头部右边设置 -->
          <div class="ratingRight">
            <div class="line"></div>
            <div class="serviceScore">
              <div class="p">服务态度</div>
              <Star :score="serviceScore" ref="serviceScore"> </Star>
              <p>{{seller.serviceScore}}</p>
            </div>
            <div class="foodScore">
              <div class="p"> 食物评分</div>
              <Star :score="foodScore" ref="foodScore"> </Star>
              <p>{{seller.foodScore}}</p>
            </div>
            <div class="deliveryTime">
              <p>送达时间</p>
              <p class="detailTime"> {{seller.deliveryTime}}分钟</p>
            </div>
          </div>
        </div>
        <Lineshow></Lineshow>
      <!-- 评价详情引入 -->
        <ratingdetail :ratings="ratings" v-if="ratings" @refresh="refresh"></ratingdetail>
      </div>
    </div>

    <!-- <shopping :goods="goods" v-if="goods.length>0"></shopping> -->
  </div>
</template>
<script>
import shopping from '../shopping/shopping'
import Vue from 'vue'
import Element from 'element-ui'
import 'element-ui/lib/theme-default/index.css'
import Star from '../star/star'
import ratingdetail from '../ratingDetail/ratingdetail'
import { mapGetters } from 'vuex'
import Lineshow from '../Line/line.vue'
export default {
  data() {
    return {
      comTime: ""
    }
  },
  computed: {
    ...mapGetters([
      'ratings',
      'seller',
      'goods'

    ]),
    serviceScore() {
      return this.seller.serviceScore
    },
    foodScore() {
      return this.seller.foodScore
    },
  },
  methods: {
    refresh() {
      setTimeout(() => {
        this.scroll && this.scroll.refresh()
      }, 20)
    },
    //滑动事件
    _scroll(f) {
      this.$nextTick(() => {
        setTimeout(() => {
          this.scroll = new BScroll(this.$refs[f], {
            probeType: 3,
            click: true
          })
        }, 20)
      })
    },
     //设置星星大小
    _getStarsecs(h, s, f) {
      setTimeout(() => {
        let m = this.$refs[f].$el;
        if (m.children) {
          for (let i = 0; i < m.children.length; i++) {
            m.children[i].style.width = h + "rem";
            m.children[i].style.height = s + "rem"
            m.children[i].style.margin = 0.05 + "rem"
          }
        }

      }, 70)
    },
  },
  components: {
    Star,
    Lineshow,
    ratingdetail,
    shopping
  },
  created() {
    this.$store.dispatch('getRatings');
    this.$store.dispatch('getSeller');
    this._getStarsecs(0.36, 0.36, "serviceScore");
    this._getStarsecs(0.36, 0.36, "foodScore");
    this._scroll("ratingDetails");
  }

}

</script>
<style lang="less" rel="stylesheet/less" type="text/less" scoped>
.ratings {
  box-sizing: border-box;
  position: absolute;
  top: 3.48rem;
  width: 7.5rem;
  height: 65%;
  left: 0;
  overflow: hidden;
//头部设置
  .detailHead {
    position: relative;
    display: flex;
    width: 100%; 
    //左边设置
    .ratingLeft {
      float: left;
      flex: 1;
      width: 2.75rem;
      text-align: center;
      .score {
        text-align: center;
        position: relative;
        font-size: 0.48rem;
        color: rgb(255, 153, 0);
        line-height: 0.48rem;
        top: 0.36rem;
      }
      .totalRatings {
        position: relative;
        font-size: 0.24rem;
        color: rgb(7, 17, 27);
        line-height: 0.24rem;
        top: 0.48rem;
      }
      .rankRate {
        position: relative;
        font-size: 0.2rem;
        color: rgb(147, 153, 159);
        line-height: 0.2rem;
        top: 0.64rem;
      }
    }

 //右边设置
    .ratingRight {
      flex: 2;
      display: inline-block;
      float: right;
      position: relative;
      width: calc(100% - 2.75rem); // height: 2.04rem;
      .line {
        position: relative;
        width: 0.05rem;
        height: 1.32rem;
        background: rgb(147, 153, 159);
        box-sizing: border-box;
        margin: 0.36rem 0 0.36rem;
      }
      .serviceScore {
        position: absolute;
        top: 0.36rem;
        left: 0.36rem;
        display: flex;
        font-size: 0;
        .p {
          height: 0.36rem;
          display: inline-block;
          font-size: 0.24rem;
          color: rgb(7, 17, 27);
          line-height: 0.36rem;
        }
        .star {
          // display: inline-block;
          margin: 0 0.24rem 0 0.24rem;
          padding: 0;
          margin-top: -0.12rem;
        }
        p {
          font-size: 0.36rem;
          color: rgb(255, 153, 0);
          line-height: 0.36rem;
        }
      }
      .foodScore {
        position: absolute;
        top: 0.76rem;
        left: 0.36rem;
        display: flex;
        font-size: 0;
        .p {
          font-size: 0.24rem;
          color: rgb(7, 17, 27);
          line-height: 0.36rem; // height: 0.36rem;
          display: inline-block;
        }
        .star {
          box-sizing: border-box;
          margin: 0 0.24rem 0 0.24rem;
          padding: 0; // display: inline-block;
          position: relative;
          margin-top: -0.12rem;
        }
        p {
          font-size: 0.36rem;
          color: rgb(255, 153, 0);
          line-height: 0.36rem;
        }
      }
      .deliveryTime {
        position: absolute;
        font-size: 0.24rem;
        color: rgb(147, 153, 159);
        line-height: 0.36rem;
        display: flex;
        top: 1.2rem;
        left: 0.36rem;
        p {
          color: rgb(7, 17, 27);
        }
        .detailTime {
          color: rgb(147, 153, 159);
          position: relative;
          left: 0.24rem;
        }
      }
    }
  }
  #line {
    position: absolute;
    top: 2.04rem;
  }
}
</style>




